<template>
    <div id="my">
      <Header :Flagadd=false :FlagHome=false :FlagCity=false :FlagSignin=false :FlagSearch=false :FlagOrder=false :FlagMy=true :FlagAcc=false>

      </Header>
      <div class="content">

        <div class="con" v-if="this.$store.state.val=='' " >
          <div class="con1"><img src="../style/img/1.jpg" /> </div>
          <div class="con2">
            <div class="con2-1" @click="deng()">
              <span>注册/</span>
              <span>登录</span>
            </div>
            <div class="con2-2"><van-icon name="hotel-o" />暂无绑定手机号</div>
          </div>
          <div class="con3"><van-icon name="arrow" /></div>
        </div>

        <div class="con" v-if="this.$store.state.val!='' "  >
          <div class="con1"><img :src="'//elm.cangdu.org/img/'+this.$store.state.img" /></div>
          <div class="con2">
            <div class="con2-1">
              <span>{{this.$store.state.val.username}}</span>
            </div>
            <div class="con2-2"><van-icon name="hotel-o" />暂无绑定手机号</div>
          </div>
          <div class="con3" @click="click()"><van-icon name="arrow" /></div>
        </div>

        <div class="cont">
          <div class="cont1">
            <p class="cont2">
              <span>0.00</span>
              <span>元</span>
            </p>
            <p class="cont3">我的余额</p>
          </div>
          <div class="cont1">
            <p class="cont2">
              <span>0</span>
              <span>个</span>
            </p>
            <p class="cont3">我的优惠</p>
          </div>
          <div class="cont1">
            <p class="cont2">
              <span>0</span>
              <span>分</span>
            </p>
            <p class="cont3">我的积分</p>
          </div>
        </div>

        <div class="contt">
          <div class="contt1">
            <div class="contt2"><van-icon name="label-o" /></div>
            <div class="contt3">
              <span>我的订单</span>
              <span><van-icon name="arrow" /></span>
            </div>
          </div>
          <div class="contt1">
            <div class="contt2"><van-icon name="label-o" /></div>
            <div class="contt3">
              <span>积分商城</span>
              <span><van-icon name="arrow" /></span>
            </div>
          </div>
          <div class="contt1">
            <div class="contt2"><van-icon name="label-o" /></div>
            <div class="contt3">
              <span>饿了么会员卡</span>
              <span><van-icon name="arrow" /></span>
            </div>
          </div>
        </div>

        <div class="contt">
          <div class="contt1">
            <div class="contt2"><van-icon name="label-o" /></div>
            <div class="contt3">
              <span>服务中心</span>
              <span><van-icon name="arrow" /></span>
            </div>
          </div>
          <div class="contt1">
            <div class="contt2"><van-icon name="label-o" /></div>
            <div class="contt3">
              <span>下载饿了么APP</span>
              <span><van-icon name="arrow" /></span>
            </div>
          </div>
        </div>

      </div>

    </div>
</template>

<script>
import Header from '../components/Header' 
export default {
  components:{
        Header
    },
  name: 'Order',
 
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  created(){

  },
  methods:{
      click(){
        this.$router.push('./Account')
      },
      deng(){
        this.$router.push('./Signin')
      }
  },
  mounted(){
    
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
#my{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: #F5F5F5;
    .content{
      margin-top: 2.01rem;
      .con{
        width: 100%;
        background:#3190e8;
        height:3rem;
        color: white;
        display:flex;
        padding-top:0.5rem;
        position: relative;
        .con1{
          margin-left: 5%;
          img{
            width:2.5rem;
            height:2.5rem;
            border-radius:50%;
            
          }
        }
        .con2{
          margin-left:2%;
          margin-top:0.1rem;
          .con2-1{
            font-size:0.8rem;
          }
          .con2-2{
            margin-top:0.3rem;
          }
        }
        .con3{
           position: absolute;
           right: 1rem;
           top:1.3rem;
        }
      }
      .cont{
        background:white;
        width:100%;
        height:3.3rem;
        display:flex;
        .cont1{
          width:33.3%;
          height:100%;
          border-right:0.01rem solid gainsboro;
          text-align:center;
          .cont2{
            padding-top:0.3rem;
            span:nth-of-type(1){
              color:#f90;
              font-weight: 700;
              font-size: 1rem;
            }
          }
          .cont3{
            padding-top:0.2rem;
            color:grey;
          }
          
        }
        .cont1:nth-of-type(2){
          .cont2{
            span:nth-of-type(1){
              color:#ff5f3e;
              font-weight: 700;
              font-size: 1rem;
            }
          }
        }
        .cont1:nth-of-type(3){
          border:none;
          .cont2{
            span:nth-of-type(1){
              color:#6ac20b;
              font-weight: 700;
              font-size: 1rem;
            }
          }
        }
      }
      .contt{
        margin-top:0.3rem;
        width:100%;
        background:white;
        .contt1{
          width:100%;
          display:flex;
          line-height: 2rem;
          .contt2{
            width:5%;
            margin-left:5%;
          }
          .contt3{
            width:90%;
            margin-left:2%;
            border-bottom:0.01rem solid gainsboro;
            position:relative;
            span:nth-of-type(2){
              position:absolute;
              top:0.2rem;
              right: 0.5rem;
              color:grey;
            }
          }
        }
        .contt1:nth-of-type(3){
          .contt3{
            border:none;
            
          }
          
        }
      }
      
    }
}
    
</style>



